
export default {
  props: {
    companyListings: {
      type: Array,
      default: () => [],
    },
    displayType: {
      type: String,
      default: null,
    },
  },
  methods: {
    buyShare(listingid) {
      this.$emit('buy-share', listingid);
    },
    sellShare(listingid) {
      this.$emit('sell-share', listingid);
    },
    editListing(listing) {
      this.$emit('edit-listing', listing);
    }
  },
};
